</template>

<script>
    export default {
        data() {
            return {
                inputValue: '',
                tableData: [{data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}, {data: 'xxxxxxxx'}]
            }
        },
        methods: {
            query() {

            }
        }
    }
</script>


